.app-header {
  width: 100%;
  font-size: 16px;
  color: #fff;
  background-color: #468259;
  height: 50px;
  line-height: 50px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0; }
  .app-header .content-wrap {
    max-width: 1440px;
    display: flex;
    margin: 0 auto;
    padding: 0 20px; }
  .app-header a {
    color: #EBEBEB;
    display: inline-block; }
  .app-header a.logo {
    font-weight: bold;
    font-size: 20px;
    width: 140px; }
    .app-header a.logo.active {
      color: #ED592A; }
  .app-header a.nav-item {
    padding: 0 15px; }
    .app-header a.nav-item:hover {
      color: #fff; }
    .app-header a.nav-item.active {
      font-weight: bold;
      color: #fff; }
