body, html {
  background-color: #F9F9F9; }

.docpage-body {
  padding: 0 20px;
  margin-top: 95px;
  height: 100%; }

.category-block {
  margin-bottom: 20px;
  width: 200px; }
  .category-block h3 {
    font-weight: 600;
    color: #393939;
    font-size: 18px;
    margin-bottom: 6px; }
  .category-block a {
    display: block;
    padding: 4px 0;
    padding-left: 10px;
    color: #717171;
    transition: color .3s; }
    .category-block a:hover {
      color: #2e8555; }
    .category-block a.active {
      color: #2e8555; }
  .category-block .block-header {
    vertical-align: middle;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer; }
